import Typography from '@mui/material/Typography';
import { light, dark } from './style';

interface SectionTitleProps {
  children: React.ReactNode;
  align?: 'left' | 'center' | 'right';
  theme?: 'light' | 'dark';
}

/**
 * SectionTitle component to display section titles with customizable alignment and theme.
 * @param {SectionTitleProps} props - The properties for the SectionTitle component.
 * @returns {JSX.Element} The rendered SectionTitle component.
 */
export const SectionTitle = ({
  children,
  align = 'center',
  theme = 'light'
}: SectionTitleProps) => {
  const isLight: boolean = theme === 'light';

  return (
    <Typography
      variant='h2'
      align={align}
      sx={{
        mb: 4,
        color: isLight ? dark : light
      }}
    >
      {children}
    </Typography>
  );
};
