import * as React from 'react';
import Container from '@mui/material/Container';
import { light, dark } from './style';

interface SectionProps {
  children: React.ReactNode;
  id?: string;
  sx?: object;
  maxWidth?: 'xs' | 'sm' | 'md' | 'lg' | 'xl' | false;
  theme?: 'light' | 'dark';
}

/**
 * Section component to wrap content with a styled container.
 * @param {SectionProps} props - The properties for the section.
 * @returns {JSX.Element} The rendered section component.
 */
export const Section = ({
  children,
  id,
  sx = {},
  maxWidth = 'lg',
  theme = 'light'
}: SectionProps) => {
  const isLight = theme === 'light';
  const isFull = maxWidth === false;

  return (
    <Container
      id={id}
      maxWidth={false}
      disableGutters={isFull}
      sx={{
        py: 8,
        backgroundColor: isLight ? light : dark,
        color: isLight ? dark : light,
        ...sx
      }}
    >
      <Container maxWidth={maxWidth} disableGutters={isFull}>
        {children}
      </Container>
    </Container>
  );
};
