import * as React from 'react';
import { useStaticQuery, graphql } from 'gatsby';
import loadable from '@loadable/component';
import Box from '@mui/material/Box';
import { createTheme, ThemeProvider } from '@mui/material/styles';
import type { Theme } from '@mui/material/styles';
import CssBaseline from '@mui/material/CssBaseline';
import { Seo } from '../components/seo';

import { SectionMenu } from '../components/sections/menu';
import { SectionMainvisual } from '../components/sections/mainvisual';
import { SectionAbout } from '../components/sections/about';
import { SectionAccess } from '../components/sections/access';
import { SectionContact } from '../components/sections/contact';

const theme: Theme = createTheme({
  palette: {
    primary: {
      main: '#b28223'
    },
    background: {
      default: 'transparent'
    }
  },
  typography: {
    fontSize: 16,
    fontFamily: ['Noto Serif JP', 'YuMincho', 'serif'].join(','),
    allVariants: {
      lineHeight: 1.8
    },
    h2: {
      fontSize: 28
    },
    h4: {
      letterSpacing: 1.5
    },
    h5: {}
  },
  components: {
    MuiCssBaseline: {
      styleOverrides: {
        body: {
          backgroundColor: '#000',
          color: '#fff',
          lineHeight: 1.8
        }
      }
    },
    MuiLink: {
      styleOverrides: {
        root: {
          color: 'inherit'
        }
      }
    }
  }
});

const IndexPage = () => {
  const {
    site: {
      siteMetadata: { title }
    }
  } = useStaticQuery(graphql`
    query {
      site {
        siteMetadata {
          title
        }
      }
    }
  `);

  const duration = 1500;
  const delay = 250;

  const [isLoaded, setIsLoaded] = React.useState(false);
  const [isEnded, setIsEnded] = React.useState(false);

  const onLoad = () => {
    setIsLoaded(true);
    setTimeout(() => {
      setIsEnded(true);
    }, duration + delay);
  };

  React.useEffect(() => {
    document.readyState === 'complete'
      ? onLoad()
      : window.addEventListener('load', onLoad);
  }, []);

  return (
    <ThemeProvider theme={theme}>
      <CssBaseline />
      {!isEnded && (
        <Box
          position='fixed'
          sx={{
            backgroundColor: '#000',
            transition: `opacity ${duration}ms ease-out ${delay}ms`,
            opacity: isLoaded ? 0 : 1,
            width: '100vw',
            height: '100vh',
            zIndex: 2000,
            pointerEvents: 'none'
          }}
        />
      )}
      <SectionMenu />
      <SectionMainvisual />
      <SectionAbout />
      <SectionAccess />
      <SectionContact />
    </ThemeProvider>
  );
};

export default IndexPage;

export const Head = () => {
  return <Seo />;
};
