import * as React from 'react';
import AppBar from '@mui/material/AppBar';
import Box from '@mui/material/Box';
import Container from '@mui/material/Container';
import IconButton from '@mui/material/IconButton';
import Link from '@mui/material/Link';
import List from '@mui/material/List';
import ListItemButton from '@mui/material/ListItemButton';
import ListItemText from '@mui/material/ListItemText';
import SwipeableDrawer from '@mui/material/SwipeableDrawer';
import Typography from '@mui/material/Typography';
import LaunchIcon from '@mui/icons-material/Launch';
import MenuIcon from '@mui/icons-material/Menu';
import PhoneIcon from '@mui/icons-material/Phone';
import { darkTheme, lightTheme } from '../style';
import Logo from '../../images/logo.png';
import { scrollToTop, scrollToSection } from '../../utils/scroll';

interface MenuList {
  label: string;
  link: string;
  isExternal?: boolean;
}

const menuLists: MenuList[] = [
  {
    label: 'About',
    link: '#about'
  },
  {
    label: 'Menu',
    link: '#menu'
  },
  {
    label: 'Access',
    link: '#access'
  },
  { label: 'Contact', link: '#contact' }
  /*
  {
    label: 'Menu',
    link: 'https://www.instagram.com/grand_reve_osaka/',
    isExternal: true
  }
  */
];

const MenuLink = (props: any): React.JSX.Element => (
  <Link
    underline='hover'
    {...props}
    sx={{
      margin: '0 1em',
      display: {
        sm: 'inline',
        xs: 'none'
      },
      ...props.sx
    }}
  >
    <Typography component='span' variant={props.variant || 'body2'}>
      {props.children}
    </Typography>
  </Link>
);

interface SectionMenuProps {
  tel?: string;
}

/**
 * SectionMenu component
 * Displays the navigation menu for the website.
 */
export const SectionMenu = ({ tel }: SectionMenuProps) => {
  const [isOpen, setIsOpen] = React.useState(false);

  const toggleDrawer = (_isOpen: boolean) => (event: any) => {
    if (
      event &&
      event.type === 'keydown' &&
      (event.key === 'Tab' || event.key === 'Shift')
    ) {
      return;
    }

    setIsOpen(_isOpen);
  };

  return (
    <AppBar position='fixed' sx={lightTheme}>
      <Container>
        <Box
          sx={{
            display: 'flex',
            justifyContent: 'space-between',
            alignItems: 'center',
            height: '68px'
          }}
        >
          <Box>
            <Link
              display='flex'
              alignItems='center'
              href='#top'
              onClick={e => {
                e.preventDefault();
                scrollToTop();
              }}
              sx={{
                textDecoration: 'none'
              }}
            >
              <img
                src={Logo}
                alt='Grand rve'
                width={36}
                style={{
                  display: 'block',
                  pointerEvents: 'none'
                }}
              />
              <Typography fontSize={18} ml={1} fontWeight='bold'>
                Grand rve
              </Typography>
            </Link>
          </Box>
          <Box>
            {menuLists.map(({ label, link, isExternal }, i) => {
              return (
                <MenuLink
                  key={i}
                  href={link}
                  target={isExternal && '_blank'}
                  onClick={(e: any) => {
                    if (!isExternal) {
                      e.preventDefault();
                      scrollToSection(link.replace(/^#(.*)$/, '$1'));
                    }
                  }}
                >
                  {label}
                  {isExternal && (
                    <LaunchIcon sx={{ ml: 0.5, width: 12, height: 12 }} />
                  )}
                </MenuLink>
              );
            })}
            {/*
            <MenuLink
              sx={{ display: 'inline' }}
              href={`tel:+81-${tel}`}
              variant='body1'
              underline='always'
            >
              <PhoneIcon
                sx={{
                  mr: 0.5,
                  width: 14,
                  height: 14
                }}
              />
              0{tel}
            </MenuLink>
            */}
            <IconButton
              onClick={toggleDrawer(true)}
              sx={{
                display: {
                  sm: 'none',
                  xs: 'inline'
                }
              }}
            >
              <MenuIcon />
            </IconButton>
          </Box>
        </Box>
      </Container>

      <SwipeableDrawer
        anchor='right'
        open={isOpen}
        onClose={toggleDrawer(false)}
        onOpen={toggleDrawer(true)}
      >
        <Box
          sx={{
            ...lightTheme,
            width: 200,
            height: '100%'
          }}
          role='presentation'
          onClick={toggleDrawer(false)}
          onKeyDown={toggleDrawer(false)}
        >
          <List>
            {menuLists.map(
              (
                {
                  label,
                  link,
                  isExternal
                }: {
                  label: string;
                  link: string;
                  isExternal?: boolean;
                },
                i: number
              ): React.JSX.Element => (
                <ListItemButton
                  key={i}
                  href={link}
                  target={isExternal ? '_blank' : ''}
                  onClick={e => {
                    if (!isExternal) {
                      e.preventDefault();
                      scrollToSection(link.replace(/^#(.*)$/, '$1'));
                    }
                  }}
                >
                  <ListItemText
                    primary={
                      <Typography>
                        {label}
                        {isExternal && (
                          <LaunchIcon sx={{ ml: 0.5, width: 10, height: 10 }} />
                        )}
                      </Typography>
                    }
                  />
                </ListItemButton>
              )
            )}
          </List>
        </Box>
      </SwipeableDrawer>
    </AppBar>
  );
};
